.header {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0;
    background-color: #10e956 !important;
    z-index: 100;
    max-width: 100%;
    height: 10vh;
    position: sticky;
    top: 0;
    animation: fadeInDown 1s ease forwards;
    @media (min-width: 760px) and (max-width: 1024px) {
        max-width: 100%;
    }
    @media (min-width: 480px) and (max-width: 759px) {
        max-width: 100%;
    }
    @media (max-width: 479px) {
        max-width: 100%;
    }
    .logo {

        cursor: pointer !important;

        margin: 0 2rem;
        padding: 0;
        z-index: 500;
        left: 5rem;
        top: 7px;
        @media (min-width: 1025px) and (max-width: 1300px) {
            // width: 86px;
            // height: 87px;
            left: 5rem;
            top: 5px;
        }
        @media (min-width: 760px) and (max-width: 1024px) {
            // width: 79px;
            // height: 80px;
            left: 2rem;
            top: 6px;
        }
        @media (min-width: 480px) and (max-width: 759px) {
            // width: 70px;
            // height: 71px;
            left: 1.8rem;
            top: 9px;
        }
        @media (max-width: 479px) {
            // width: 60px;
            // height: 61px;
            left: 1.5rem;
            top: 9px;
        }
        img {
            width: 70px;
            height: 70px;
            @media (min-width: 1025px) and (max-width: 1300px) {
                width: 65px;
                height: 65px;
            }
            @media (min-width: 760px) and (max-width: 1024px) {
                width: 60px;
                height: 60px;
            }
            @media (min-width: 480px) and (max-width: 759px) {
                width: 55px;
                height: 55px;
            }
            @media (max-width: 479px) {
                width: 55px;
                height: 55px;
            }
        }
    }
    .menu-toggle {
        transform: none !important;
        .nav__checkbox {
            display: none;
            &:checked ~ .navigation {
                opacity: 1;
                max-width: 100%;
                width: 100%;
                pointer-events: auto;
                .large_menu {
                    // animation: fadeIn 4s ease forwards 0s;
                    a {
                        flex-grow: 1 !important;
                    }
                    .github {
                        animation: zoomIn 0.5s ease forwards;
                        opacity: 1;
                        transition: opacity 0.5s;
                    }
                    .linkedin {
                        animation: zoomIn 1s ease forwards;
                        opacity: 1;
                        transition: opacity 1s;
                    }
                    .twitter {
                        animation: zoomIn 1.5s ease forwards;
                        opacity: 1;
                        transition: opacity 1.5s;
                    }
                    .schedule {
                        animation: zoomIn 2s ease forwards;
                        opacity: 1;
                        transition: opacity 2s;
                    }
                }
            }
            &:checked + .nav__button .nav__icon {
                background-color: transparent;
            }
            &:checked + .nav__button .nav__icon::before {
                transform: rotate(135deg);
                top: 0;
                width: 3.5rem;
                @media (min-width: 480px) and (max-width: 759px) {
                    width: 2.5rem;
                    background-color: #10e956;
                }
                @media (max-width: 479px) {
                    width: 2.5rem;
                    background-color: #10e956;
                }
            }
            &:checked + .nav__button .nav__icon::after {
                transform: rotate(-135deg);
                top: 0;
                width: 3.5rem;
                @media (min-width: 480px) and (max-width: 759px) {
                    width: 2.5rem;
                    background-color: #10e956;
                }
                @media (max-width: 479px) {
                    width: 2.5rem;
                    background-color: #10e956;
                }
            }
        }
        .nav__button {
            position: fixed;
            right: 5rem;
            top: 15px;
            height: 45px;
            width: 60px;
            z-index: 2000;
            border-radius: 5px;
            cursor: pointer;
            display: flex;
            justify-content: center;
            align-items: center;
            animation: wobble 1s ease forwards 10s;
            animation-iteration-count: 3;
            @media (min-width: 1025px) and (max-width: 1300px) {
                width: 50px;
                height: 38px;
                padding: 0.8rem;
                right: 5rem;
            }
            @media (min-width: 760px) and (max-width: 1024px) {
                width: 42px;
                height: 32px;
                padding: 0.7rem;
                right: 2rem;
            }
            @media (min-width: 480px) and (max-width: 759px) {
                width: 40px;
                height: 30px;
                padding: 0.6rem;
                right: 1.8rem;
            }
            @media (max-width: 479px) {
                width: 35px;
                height: 25px;
                padding: 0.5rem;
                right: 1.5rem;
            }
            &:hover .nav__icon::before {
                top: -1rem;
            }

            &:hover .nav__icon::after {
                top: 1rem;
            }
            .nav__icon {
                position: relative;
                margin-right: -0.8rem;
                width: 2.7rem;
                height: 8px;
                background-color: #1e2128;
                display: inline-block;
                transform: rotate(0.5turn);
                border-radius: 4px;
                @media (min-width: 1025px) and (max-width: 1300px) {
                    width: 2rem;
                    height: 8px;
                }
                @media (min-width: 760px) and (max-width: 1024px) {
                    width: 1.7rem;
                    height: 6px;
                }
                @media (min-width: 480px) and (max-width: 759px) {
                    width: 1.5rem;
                    height: 6px;
                }
                @media (max-width: 479px) {
                    width: 1.3rem;
                    height: 6px;
                }
                &::before {
                    width: 2rem;
                    height: 8px;
                    background-color: #1e2128;
                    display: inline-block;
                    border-radius: 4px;
                    @media (min-width: 1025px) and (max-width: 1300px) {
                        width: 3rem;
                        height: 8px;
                    }
                    @media (min-width: 760px) and (max-width: 1024px) {
                        width: 2.7rem;
                        height: 6px;
                    }
                    @media (min-width: 480px) and (max-width: 759px) {
                        width: 2.3rem;
                        height: 6px;
                    }
                    @media (max-width: 479px) {
                        width: 2.1rem;
                        height: 6px;
                    }
                }
                &::after {
                    width: 3.5rem;
                    height: 8px;
                    background-color: #1e2128;
                    display: inline-block;
                    border-radius: 4px;
                    @media (min-width: 1025px) and (max-width: 1300px) {
                        width: 3rem;
                        height: 8px;
                    }
                    @media (min-width: 760px) and (max-width: 1024px) {
                        width: 2.7rem;
                        height: 6px;
                    }
                    @media (min-width: 480px) and (max-width: 759px) {
                        width: 2.3rem;
                        height: 6px;
                    }
                    @media (max-width: 479px) {
                        width: 2.1rem;
                        height: 6px;
                    }
                }

                &::before,
                &::after {
                    content: "";
                    position: absolute;
                    left: 0;
                    transition: all 0.2s;
                }

                &::before {
                    top: -0.8rem;
                    @media (min-width: 480px) and (max-width: 759px) {
                        top: -0.7rem;
                    }
                    @media (max-width: 479px) {
                        top: -0.7rem;
                    }
                }

                &::after {
                    top: 0.8rem;
                    @media (min-width: 480px) and (max-width: 759px) {
                        top: 0.7rem;
                    }
                    @media (max-width: 479px) {
                        top: 0.7rem;
                    }
                }
            }
        }
        .navigation {
            height: 100vh;
            position: fixed;
            top: 0;
            left: 0;
            z-index: 1000;
            background: #14e956;
            display: flex;
            align-items: center;
            justify-content: center;
            opacity: 0;
            width: 0;
            padding: 0;
            pointer-events: none;
            display: grid;
            grid-template-columns: 1fr 1fr;
            grid-template-rows: 1fr 3fr;
            grid-template-areas:
                "box ."
                "box menu";
            @media (min-width: 760px) {
                display: grid;
                grid-template-columns: 1fr 1fr;
                grid-template-rows: 1fr 3fr;
                grid-template-areas:
                    "box ."
